.todos {
  height: 70vh; /* Magic here */
  display: flex;
  flex-direction: column;
}

.todos h1 {
  margin: 2px;
  font-size: 7rem;
  justify-content: center;
  align-items: center;
  z-index: 1;
  font-family: 'Montserrat', sans-serif;
}

.todos h2 {
  margin: -40px;
  font-size: 10rem;
  z-index: 1;
  color: #fff;
  font-family: 'Oxygen', sans-serif;
}

.todos a {
  text-decoration: none;
  top: 20%;
  position: relative;
  color: #00FFE5;
  font-family: 'Montserrat', sans-serif;
  font-size: 1.5rem;
  display: inline-flex;
  border-bottom: 1px solid #00FFE5;
  cursor: pointer;
  z-index: 10;
  transition: 0.3s all ease;
}

.todos a:hover {
  color: #fff !important;
  border-bottom: 1px solid #fff;
  transition: 0.2s all ease;

}



.todos .blue {
  color: #00FFE5;
}




