@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400&family=Oxygen:wght@700&display=swap');


*,::after,::before{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

.todos {
  height: 70vh; /* Magic here */
  display: flex;
  flex-direction: column;
}

.todos h1 {
  margin: 2px;
  font-size: 7rem;
  justify-content: center;
  align-items: center;
  z-index: 1;
  font-family: 'Montserrat', sans-serif;
}

.todos h2 {
  margin: -40px;
  font-size: 10rem;
  z-index: 1;
  color: #fff;
  font-family: 'Oxygen', sans-serif;
}

.todos a {
  text-decoration: none;
  top: 20%;
  position: relative;
  color: #00FFE5;
  font-family: 'Montserrat', sans-serif;
  font-size: 1.5rem;
  display: inline-flex;
  border-bottom: 1px solid #00FFE5;
  cursor: pointer;
  z-index: 10;
  transition: 0.3s all ease;
}

.todos a:hover {
  color: #fff !important;
  border-bottom: 1px solid #fff;
  transition: 0.2s all ease;

}



.todos .blue {
  color: #00FFE5;
}




@media (min-width: 1025px) {
    
    .todos h1 {
      margin: 2px;
      font-size: 7rem;
      justify-content: center;
      align-items: center;
      z-index: 1;
      font-family: 'Montserrat', sans-serif;
    }
    
    .todos h2 {
      margin: -40px;
      font-size: 10rem;
      z-index: 1;
      color: #fff;
      font-family: 'Oxygen', sans-serif;
    }
  
}



@media (min-width: 768px) and (max-width: 1024px) {
  
  .todos h1 {
    margin: 2px;
    font-size: 6rem;
    justify-content: center;
    align-items: center;
    z-index: 1;
    font-family: 'Montserrat', sans-serif;
  }
  
  .todos h2 {
    margin: -40px;
    font-size: 9rem;
    z-index: 1;
    color: #fff;
    font-family: 'Oxygen', sans-serif;
  }
  
}




@media (min-width: 481px) and (max-width: 767px) {
  .todos h1 {
    margin: 2px;
    font-size: 5rem;
    justify-content: center;
    align-items: center;
    z-index: 1;
    font-family: 'Montserrat', sans-serif;
  }
  
  .todos h2 {
    margin: -40px;
    font-size: 8rem;
    z-index: 1;
    color: #fff;
    font-family: 'Oxygen', sans-serif;
  }
  
}




@media (min-width: 320px) and (max-width: 480px) {
  
  .todos h1 {
    margin: 2px;
    font-size: 4rem;
    justify-content: center;
    align-items: center;
    z-index: 1;
    font-family: 'Montserrat', sans-serif;
  }
  
  .todos h2 {
    margin: -40px;
    font-size: 7rem;
    z-index: 1;
    color: #fff;
    font-family: 'Oxygen', sans-serif;
  }
  
}